import React from 'react';
import { graphql } from 'gatsby';
import Helmet from 'react-helmet';
import IndexLayout from '../../layouts';
import { Page } from '../../components/layout/Page';
import styled from 'styled-components';
import { SiteMetadata } from '../../interfaces/gatsby';

import { DocsWrapper } from '../../components/docs/DocsWrapper';
import { Container } from '../../components/layout/Container';
import { PALDocsHeader } from '../../components/docs/DocsHeader';
import { MarkdownContent } from '../../components/page/Markdown';
import { FooterWrapper, PALFooter } from 'components/layout/Footer';
import { BackToTopButton } from 'components/docs/BackToTopButton';
import SEO from '../seo';
import {
  HeaderPara,
  Para,
  Card,
  CardHeader,
  CardImage,
  CardTechImage,
  CardContent,
  CardSection
} from '../../components/styles';
import { Heading, Paragraph } from 'components/foundations';

import LevelingUp from 'assets/images/svg/sa_levelingup.svg';
import Certified from 'assets/images/svg/sa_Certified.svg';
import BIImplementation from 'assets/images/svg/BIImplementation.svg';
import BISupport from 'assets/images/svg/BISupport.svg';
import innovation from 'assets/images/svg/innovation.svg';

import Tensorflow from 'assets/images/technologies/tensorflow-ar21.svg';
import Python from 'assets/images/technologies/python-ar21.svg';

import PowerBI from 'assets/images/technologies/microsoft_powerbi-ar21.svg';
import Pytorch from 'assets/images/technologies/pytorch-ar21.svg';
import SQLite from 'assets/images/technologies/sqlite-ar21.svg';
import RProject from 'assets/images/technologies/r-project-ar21.svg';
import Jupyter from 'assets/images/technologies/jupyter-ar21.svg';
import Pandas from 'assets/images/technologies/usepanda-ar21.svg';
import NodeJS from 'assets/images/technologies/nodejs-ar21.svg';

interface Props {
  data: {
    site: {
      siteMetadata: SiteMetadata;
    };
  };
}

const Container1 = styled(Container)`
  background: #f9f9fc;
  // margin-top: 00px;
  // padding-top: 40px;
  // margin-bottom: 40px;
  // padding: 10px 10px;
`;

const FluidCard = styled('div')`
  background-color: #b2eae8;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  -moz-box-pack: justify;
  justify-content: space-between;
  box-shadow: rgba(12, 15, 20, 0.02) 0px 0px 0px 1px, rgba(12, 15, 20, 0.06) 0px 0px 1px 0px,
    rgba(12, 15, 20, 0.06) 0px 2px 2px 0px;
  max-width: 344px;
  margin: 24px auto;

  @media (min-width: 768px) {
    flex-direction: row;
    max-width: 80%;
  }
`;

const Img = styled('img')`
  background-color: #b2eae8;
  padding: 16px;
  object-fit: contain;
  height: 150px;
  width: 100%;
  aspect-ratio: 159 / 92; /* Amounts close to a 184px height */
  margin: auto;

  @media (min-width: 768px) {
    flex-grow: 0;
    flex-shrink: 0;
    align-self: stretch;
    height: 100%;
    width: 30%;
  }
`;

const Content = styled('div')`
  width: 100%;
  padding: 20px;
`;

const H2 = styled('h2')`
  color: #333333;
`;

const P = styled('p')`
  color: #333333;
`;

const SolutionsArchitectPage: React.FC<Props> = ({ data }) => {
  const { site } = data;
  const { siteMetadata } = site;
  const title = 'Solutions Architect';
  const description =
    'We design and maintain technology roadmaps for our clients based upon their individual requirements. We help to identify value opportunities within technology investment and are able to structure and document an effective business case for future enhancement.';
  // const url = 'https://www.palportals.com/services/business_intelligence';
  // https://css-tricks.com/fluid-images-in-a-variable-proportion-layout/
  return (
    <IndexLayout navHidden>
      <Page docsPage>
        <SEO title={title} description={description} />
        {/* <Helmet>
          <title>
            {title} &middot; {data.site.siteMetadata.title}
          </title>
          <meta property="og:title" content={title} />
          <meta name="description" content={description} />
          <meta property="og:description" content={description} />
          <meta property="og:locale" content="en_US" />
          <meta property="og:type" content="website" />
          <meta property="og:site_name" content={data.site.siteMetadata.title} />
          <meta name="twitter:card" content={title} />
          <meta name="twitter:description" content={description} />
          <meta name="twitter:title" content={title} />
          <meta property="og:url" content={url} />
        </Helmet> */}
        <DocsWrapper>
          <Container>
            <PALDocsHeader title={title} subtitle={description} />
            <MarkdownContent style={{ boxSizing: 'border-box' }}>
              <Para>
                Solution architecture is a practice of designing, describing, and managing the solution engineering in
                relation to specific business problems.
              </Para>
              <HeaderPara style={{ marginTop: '100px', marginBottom: '100px' }}>
                We offers following Solutions Architect services:
              </HeaderPara>
              <FluidCard>
                <Img src={LevelingUp} alt="Consulting" />
                <Content>
                  <H2>Leveling up from "Good" to "Great".</H2>
                  <P>
                    Solutions Architecture is the practice with which we can clearly articulate what work needs to be
                    carried out from the current to future state.
                    <br />
                    Our experienced and certified Solution Architects can provide you with expert design services and
                    independent advice which will ensure your business stays agile without comprising on security and
                    your desired timeline.
                  </P>
                  <ul>
                    <li>Enterprise architecture</li>
                    <li>Solutions architecture</li>
                    <li>Technical architecture</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <Img src={Certified} alt="Certificates" />
                <Content>
                  <H2>Certified professionals</H2>
                  <P>We offers certified experience professional to cater your requirements.</P>
                  <ul>
                    <li>Azure certified professionals</li>
                    <li>AWS certified professionals</li>
                    <li>Google cloud certified professionals</li>
                  </ul>
                </Content>
              </FluidCard>
            </MarkdownContent>
            <MarkdownContent>
              <Container1>
                <HeaderPara style={{ marginBottom: '50px', paddingTop: '50px' }}>Technologies expertise:</HeaderPara>
                <CardSection>
                  <CardTechImage>
                    <img src={Tensorflow} alt="Tensor Flow" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={Python} alt="Python" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={PowerBI} alt="Power BI" />
                  </CardTechImage>
                </CardSection>
                <CardSection>
                  <CardTechImage>
                    <img src={Pytorch} alt="Pytorch" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={SQLite} alt="SQLite" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={RProject} alt="R-Project" />
                  </CardTechImage>
                </CardSection>
                <CardSection>
                  <CardTechImage>
                    <img src={Jupyter} alt="Jupyter" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={Pandas} alt="Pandas" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={NodeJS} alt="NodeJS" />
                  </CardTechImage>
                </CardSection>
              </Container1>
            </MarkdownContent>

            <HeaderPara style={{ marginTop: '150px', marginBottom: '100px', display: 'none' }}>Case Studies</HeaderPara>
            <HeaderPara style={{ marginTop: '50px', marginBottom: '50px', display: 'none' }}>
              Recent Articles
            </HeaderPara>
            <FooterWrapper>
              <PALFooter
                version={siteMetadata.version}
                siteLastUpdated={siteMetadata.siteLastUpdated}
                socials={siteMetadata.socials}
              />
            </FooterWrapper>
          </Container>
          <BackToTopButton href="#" />
        </DocsWrapper>
      </Page>
    </IndexLayout>
  );
};

export default SolutionsArchitectPage;

export const query = graphql`
  query SolutionsArchitectPageQuery {
    site {
      siteMetadata {
        title
        sidebarTitle
        sidebarSubtext
        siteLastUpdated
        description
        version
        siteUrl
        keywords
        author {
          name
          url
          email
        }
        socials {
          name
          imgpath
          url
        }
      }
    }
  }
`;
